import { Dispatch } from "react";
import helperErrorMessage from "../../Helpers/helperErrorMessage";
import FamilyApi from "../Api/FamilyApi";
import IServerResponse from "../Interface/IServerResponse";
import { FamilyModel } from "../Models/FamilyModel";
import { FamilyReducerModel, FamilyReducerTypes } from "../Types/FamilyTypes";
import { NewsReducerTypes } from "../Types/NewsTypes";
import { PageReducerTypes } from "../Types/PageTypes";

const addFamily = (
  payload: FamilyModel,
  successCallback: (msg: string) => any
) => async (dispatch: Dispatch<NewsReducerTypes | PageReducerTypes>) => {
  try {
    dispatch({
      type: "SET_PAGE_LOADING",
      page_loading: {
        loading_message: "Loading, thank you for your patience!",
        show: true,
      },
    });
    const response: IServerResponse = await FamilyApi.addFamily(payload);
    dispatch({
      type: "SET_PAGE_LOADING",
      page_loading: {
        show: false,
      },
    });
    if (response.success) {
      if (typeof successCallback === "function") {
        successCallback(response.message.toString());
      }
      dispatch({
        type: "SET_PAGE_SNACKBAR",
        page_snackbar: {
          message: response.message.toString(),
          options: {
            variant: "success",
          },
        },
      });
    } else {
      helperErrorMessage(dispatch, response);
    }
  } catch (error) {
    console.error(`action error`, error);
  }
};

const getSingleFamily = (ulo_pamilya: null | number) => async (
  dispatch: Dispatch<FamilyReducerTypes>
) => {
  if (!ulo_pamilya) {
    dispatch({
      type: "single_fam",
      single_fam: null,
    });
    return;
  }

  try {
    dispatch({
      type: "set_single_fam",
      set_single_fam: true,
    });
    const response: IServerResponse = await FamilyApi.getSingleFamily(
      ulo_pamilya
    );

    if (response.success) {
      dispatch({
        type: "single_fam",
        single_fam: response.data,
      });
    }

    dispatch({
      type: "set_single_fam",
      set_single_fam: false,
    });
  } catch (error) {
    console.error(`action error`, error);
  }
};

export default {
  addFamily,
  getSingleFamily,
};
