import { Grid } from "@material-ui/core";
import React, { FC, memo, useState } from "react";
import { FormProvider, useForm } from "react-hook-form";
import { useDispatch } from "react-redux";
import FormDialog from "../../../Component/FormDialog/FormDialog";
import AutocompleteHookForm from "../../../Component/HookForm/AutocompleteHookForm";
import MultiRadioFieldHookForm from "../../../Component/HookForm/MultiRadioFieldHookForm";
import LoadingButton from "../../../Component/LoadingButton";
import { setSnackbar } from "../../../Services/Actions/PageActions";
import ResidentApi from "../../../Services/Api/ResidentApi";
import { FamMemberModel } from "../../../Services/Models/FamilyModel";
import * as yup from "yup";
import { yupResolver } from "@hookform/resolvers/yup";
interface IAddFamMemDialog {
  handleSetOpenDialog: (open: boolean) => void;
  open: boolean;
  handleAddFamMember: (fam_member: FamMemberModel) => void;
  fam_members: Array<FamMemberModel>;
}

const validSchema = yup.object({
  resident_pk: yup.string().required().label("Resident"),
  rel: yup.string().required().label("Relationship"),
});

export const AddFamMemDialog: FC<IAddFamMemDialog> = memo(
  ({ handleSetOpenDialog, open, handleAddFamMember, fam_members }) => {
    const dispatch = useDispatch();
    const [adding_fam_mem, set_adding_fam_mem] = useState(false);
    const form_add_fam = useForm({
      resolver: yupResolver(validSchema),
      mode: "onBlur",
    });

    const submitForm = async (formData: FamMemberModel) => {
      if (formData.resident_pk) {
        const findMem = fam_members.findIndex(
          (f) => f.resident_pk === formData.resident_pk
        );

        if (findMem) {
          set_adding_fam_mem(true);
          const response = await ResidentApi.getSingleResident(
            formData.resident_pk
          );

          console.log(`response`, response);
          set_adding_fam_mem(false);

          if (response.success) {
            formData.resident_info = response.data;
            handleAddFamMember(formData);
            handleSetOpenDialog(false);
          }
        } else {
          dispatch(
            setSnackbar(
              "Makita nani nga residente sa sakop sa pamilya!",
              "error"
            )
          );
        }
      }
    };
    return (
      <>
        <FormDialog
          open={open}
          title="Pilia ang Residente nga Sakop sa Pamilya"
          handleClose={() => handleSetOpenDialog(false)}
          body={
            <FormProvider {...form_add_fam}>
              <form
                id="add-fam-member-form"
                noValidate
                onSubmit={form_add_fam.handleSubmit(submitForm)}
                style={{ padding: `0 5em` }}
              >
                <Grid container spacing={2}>
                  <Grid item xs={12}>
                    <AutocompleteHookForm
                      endpoint="api/resident/searchResident"
                      name="resident_pk"
                      fullWidth
                      InputLabelProps={{
                        shrink: true,
                      }}
                      label="Pangalan sa Sako sa Pamilya"
                      variant="outlined"
                    />
                  </Grid>
                  <Grid item xs={12}>
                    <MultiRadioFieldHookForm
                      name="rel"
                      label="Relasyon sa Ulo sa Pamilya"
                      radio_items={[
                        {
                          value: "asawa",
                          label: "asawa",
                        },
                        {
                          value: "bana",
                          label: "bana",
                        },
                        {
                          value: "anak",
                          label: "anak",
                        },
                        {
                          label: "igsuon",
                          value: "igsuon",
                        },
                        {
                          label: "amahan",
                          value: "amahan",
                        },
                        {
                          label: "inahan",
                          value: "inahan",
                        },
                        {
                          label: "others",
                          value: "others",
                        },
                      ]}
                    />
                  </Grid>
                </Grid>
              </form>
            </FormProvider>
          }
          actions={
            <>
              <LoadingButton
                type="submit"
                variant="contained"
                form="add-fam-member-form"
                loading={adding_fam_mem}
                color="primary"
              >
                I-dungag sa Sakop sa Pamilya
              </LoadingButton>
            </>
          }
        />
      </>
    );
  }
);

export default AddFamMemDialog;
