import { TextField, TextFieldProps } from "@material-ui/core";
import { Autocomplete } from "@material-ui/lab";
import { useField } from "formik";
import React, { useEffect, useState, memo, FC } from "react";
import { Controller, useFormContext } from "react-hook-form";
import useDebounce from "../../Hooks/UseDebounce";
import { PostFetch } from "../../Hooks/UseFetch";

interface IAutocompleteHookForm {
  label?: string;
  name: string;
  // endPoint: string;
  className?: string;
  rows?: string;
  multiline?: string;
  required?: boolean;
  placeholder?: string;
  size?: "small" | "medium";
  optKeyIdType?: "string" | "number";
  disabled?: boolean;
  endpoint: string;
  defaultInputValue?: string;
}

const AutocompleteHookForm: FC<IAutocompleteHookForm & TextFieldProps> = memo(
  ({
    label,
    className,
    rows,
    multiline,
    variant,
    required,
    placeholder,
    disabled,
    name,
    endpoint,
    defaultInputValue,
    ...props
  }) => {
    const [options, setOptions] = useState<any>([]);

    const [input_val, set_input_val] = useState<string>(
      defaultInputValue ? defaultInputValue : ""
    );

    const [reload_values, set_relaod_values] = useState(0);

    const { setValue, getValues, errors } = useFormContext();

    let error = false;
    let error_message = "";

    if (errors && errors?.hasOwnProperty(name)) {
      error = true;
      error_message = errors[name]?.message;
    }

    const debouncedSearchTerm = useDebounce(input_val, 500);

    useEffect(() => {
      async function fetchData() {
        // setLoading(true);

        const changed_value = getValues()[name];
        setOptions([]);
        const response = await PostFetch(endpoint, {
          value: changed_value ? changed_value : "",
        });
        if (response.success) {
          setOptions(response.data);
        }
      }

      if (
        typeof debouncedSearchTerm === "string" &&
        debouncedSearchTerm !== null
      ) {
        fetchData();
      } else {
        setOptions([]);
      }
    }, [debouncedSearchTerm]);

    return (
      <Controller
        name={name}
        render={({ onChange, onBlur, value, ref }) => (
          <Autocomplete
            options={options}
            onChange={(event: any, newValue: any) => {
              if (newValue) {
                setValue(name, newValue.id);
              } else {
                setValue(name, "");
              }
              set_relaod_values((prev) => (prev = prev + 1));
            }}
            inputValue={input_val}
            onInputChange={(event: object, value: string, reason: string) => {
              if (event) {
                set_input_val(value);
              }
            }}
            getOptionLabel={(option) => option.label}
            renderInput={(params) => (
              <TextField
                {...params}
                name={name}
                InputLabelProps={props.InputLabelProps}
                label={label}
                variant={"outlined"}
                error={error}
                helperText={error_message}
              />
            )}
          />
        )}
      />
    );
  }
);

export default AutocompleteHookForm;
